import React from 'react';

class SearchBox extends React.Component{
    constructor(props){
        super(props);
        this.onFormSubmit = this.onFormSubmit.bind(this);
         
    }
    
    state = {term:''}; 

    /* onInputChange(event){
        console.log(event.target.value)
    } */

    onFormSubmit(e){
        e.preventDefault();
        this.props.onSubmit(this.state.term);
        // console.log(this.state.term)
    }
    render (){
        return(
            <form onSubmit={this.onFormSubmit}><div className="ui segment top">
                <input value={this.state.term} className="pa2 ba b--green bg-lightest-blue" type="search" placeholder="Search here" onChange={e=>this.setState({term: e.target.value})}></input>
            </div></form> 
        )
    }
}

export default SearchBox;